<template>
  <div>
    <h1>{{ id ? "" : "" }}</h1>
    <el-form label-width="200px" @submit.native.prevent="save">
      <el-form-item label="">
        <el-input v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="model.new_name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-avatar shape="square" :size="50" :src="model.avatar"></el-avatar>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="model.gender" placeholder="">
          <el-option
            label=""
            value="0">
          </el-option>
          <el-option
            label=""
            value="1">
          </el-option>
          <el-option
            label=""
            value="2">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="model.is_friend"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="model.province"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="model.city"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="model.remark"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: {}
  },
  data() {
    return {
      model: {}
    };
  },
  methods: {
    async save() {
      if (this.id) {
        await this.$http.put(`rest/customers/${this.id}`, this.model);
      } else {
        await this.$http.post("rest/customers", this.model);
      }
      this.$router.push("/customer/list");
      this.$message({
        type: "success",
        message: ""
      });
    },
    async fetch() {
      const res = await this.$http.get(`rest/customers/${this.id}`);
      this.model = res.data;
    }
  },
  created() {
    this.id && this.fetch();
  }
};
</script>
