<template>
  <div>
    <m-top-handle title="id" @onSearch="onSearch" content="" path="/customer/create/"></m-top-handle>
    <p> {{items.length}}</p>
    <el-table size="mini" :data="items" :header-cell-style="{transform: 'rotateZ(-18deg)'}">
      <el-table-column prop="new_name" label="">
        <template slot-scope="scope">
          <el-avatar shape="square" :size="50" :src="scope.row.avatar"></el-avatar>
        </template>
      </el-table-column>
      <el-table-column prop="_id" label="ID"></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="new_name" label=""></el-table-column>
      <el-table-column prop="type" label="">
        <template slot-scope="scope">{{ scope.row.type==1?'':''}}</template>
      </el-table-column>
      <el-table-column prop="gender" label="">
        <template slot-scope="scope">{{ scope.row.gender==0?'':(scope.row.gender==1?'':'')}}</template>
      </el-table-column>
      <el-table-column prop="subscribe" label=""></el-table-column>
      <el-table-column prop="is_friend" label=""></el-table-column>
      <el-table-column prop="from" label="">
        <template slot-scope="scope">{{ scope.row.from=='wechat'?'':''}}</template>
      </el-table-column>
      <el-table-column prop="province" label=""></el-table-column>
      <el-table-column prop="city" label=""></el-table-column>
      <el-table-column prop="create_time" label=""></el-table-column>
      <el-table-column prop="last_time" label=""></el-table-column>
      <el-table-column prop="openid" label="openId"></el-table-column>
      <el-table-column fixed="right" label="">
        <template slot-scope="scope">
          <el-button
            type="text"
            size="small"
            icon="el-icon-edit"
            @click="$router.push(`/customer/edit/${scope.row._id}`)"
          ></el-button>
          <el-button
            type="text"
            size="small"
            icon="el-icon-delete"
            @click="remove(scope.row._id)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [],
    };
  },
  methods: {
    onSearch(v){
      let params = {
        _id: v 
      }
      this.fetch(params)
    },
    async fetch(params={}) {
      const res = await this.$http.get("rest/customers",{params:params});
      console.log(res)
      this.items = []
    },
    remove(id) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(async () => {
          await this.$http.delete(`rest/customers/${id}`);
          this.$message({
            type: "success",
            message: "!",
          });
          this.fetch();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
  },
  created() {
    this.fetch();
  },
};
</script>
