<template>
  <div>
    <m-top-handle
      title="id"
      @onSearch="onSearch"
      content=""
      path="/order/create/"
    ></m-top-handle>
    <p> {{items.length}}</p>
    <el-table :data="items">
      <el-table-column prop="order_id" label="id" ></el-table-column>
      <el-table-column prop="par" label="id" ></el-table-column>
      <el-table-column prop="customer_name" label="" ></el-table-column>
      <el-table-column prop="is_bind" label="" ></el-table-column>
      <el-table-column prop="customer_id" label="id" ></el-table-column>
      <el-table-column prop="goods_name" label="" ></el-table-column>
      <el-table-column prop="goods_num" label="" ></el-table-column>
      <el-table-column prop="platform" label=""></el-table-column>
      <el-table-column prop="from" label="">
        <!-- <template scope="scope">
          {{ scope.row.createdAt | date("YYYY-MM-DD HH:mm:ss") }}
        </template> -->
      </el-table-column>
      <el-table-column prop="real" label=""></el-table-column>
      <el-table-column prop="actualFee" label=""></el-table-column>
      <el-table-column prop="subsidyFee" label=""></el-table-column>
      <el-table-column prop="estimateCosPrice" label=""></el-table-column>
      <el-table-column prop="estimateFee" label=""></el-table-column>
      <el-table-column prop="estimateCustomerFee" label=""></el-table-column>
      <el-table-column prop="orderTime" label=""></el-table-column>
      <el-table-column prop="modifyTime" label=""></el-table-column>
      <el-table-column prop="finishTime" label=""></el-table-column>
      <el-table-column prop="isSendFee" label=""></el-table-column>
      <el-table-column fixed="right" label="">
        <template slot-scope="scope">
          <el-button
            type="text"
            size="small"
            icon="el-icon-edit"
            @click="$router.push(`/order/edit/${scope.row._id}`)"
            ></el-button
          >
          <el-button
            type="text"
            size="small"
            icon="el-icon-delete"
            @click="remove(scope.row._id)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [],
    };
  },
  methods: {
    onSearch(v){
      let params = null
      if(v){
        params = {
          customer_id: v.trim()
        }
      }else{
        params = {}
      }
      this.fetch(params)
    },
    async fetch(params={}) {
      const res = await this.$http.get("rest/orders",{params:params});
      this.items = res.data;
    },
    remove(id) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(async () => {
          await this.$http.delete(`rest/orders/${id}`);
          this.$message({
            type: "success",
            message: "!",
          });
          this.fetch();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
  },
  created() {
    this.fetch();
  },
};
</script>
