<template>
  <div>
    <m-top-handle
      title="id"
      @onSearch="onSearch"
      content=""
      path="/turnlinks/create/"
    ></m-top-handle>
    <p>{{items.length}}</p>
    <el-table :data="items">
      <el-table-column prop="customer_id" label="id"></el-table-column>
      <el-table-column prop="customer_name" label=""></el-table-column>
      <el-table-column prop="create_time" label="">
      </el-table-column>
      <el-table-column prop="origin_url" label="">
        <template slot-scope="scope">
          <el-popover
            placement="top-center"
            trigger="hover"
            :content="scope.row.origin_url">
            <span slot="reference">{{scope.row.origin_url&&scope.row.origin_url.length>20?scope.row.origin_url.slice(0,20):scope.row.origin_url}}</span>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column prop="turn_content" label="">
        <template slot-scope="scope">
          <el-popover
            placement="top-center"
            trigger="hover"
            :content="scope.row.turn_content">
            <span slot="reference">{{scope.row.turn_content&&scope.row.turn_content.length>20?scope.row.turn_content.slice(0,20):scope.row.turn_content}}</span>
          </el-popover>
        </template>
        
      </el-table-column>
      <el-table-column fixed="right" label="">
        <template slot-scope="scope">
          <el-button
            type="text"
            size="small"
            icon="el-icon-edit"
            @click="$router.push(`/turnlinks/edit/${scope.row._id}`)"
            ></el-button
          >
          <el-button
            type="text"
            size="small"
            icon="el-icon-delete"
            @click="remove(scope.row._id)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [],
    };
  },
  methods: {
    onSearch(v){
      let params = null
      if(v){
        params = {
          customer_id: v.trim()
        }
      }else{
        params = {}
      }
      this.fetch(params)
    },
    async fetch(params={}) {
      const res = await this.$http.get("rest/turnlinks",{params:params});
      this.items = res.data;
    },
    remove(id) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(async () => {
          await this.$http.delete(`rest/turnlinks/${id}`);
          this.$message({
            type: "success",
            message: "!",
          });
          this.fetch();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
  },
  created() {
    this.fetch();
  },
};
</script>
